import wax from './networks/wax'
import proton from './networks/proton'
import fio from './networks/fio'

import local from './networks/local'
import jungle from './networks/jungle'
import jungle3 from './networks/jungle3'
import kylin from './networks/kylin'
import eosTest from './networks/eos-test'
import protonTest from './networks/proton-test'
import waxTest from './networks/wax-test'
import fioTest from './networks/fio-test'
import { IConstants } from './types'

// Maps chain names to constants.
export const chainToNetworkConstantsMap: { [name: string]: IConstants } = {
  eos,
  wax,
  proton,
  local,
  jungle,
  jungle3,
  kylin,
  fio,
  ['eos-test']: eosTest,
  ['proton-test']: protonTest,
  ['wax-test']: waxTest,
  ['fio-test']: fioTest
}
export class Constants {
  [key: string]: any

  constructor () {
    if (!!(Constants as any).instance) {
      return (Constants as any).instance
    }
  }

  initialize (chain?: string) {
    if (!chain || !chainToNetworkConstantsMap[chain]) {
      chain = DEFAULT_CHAIN
    }

    this.setNetwork(chain)
    this.setCommon(chain)
  }

  setNetwork (chain: string) {
    // const networkConstants = await import(`'./networks/${chain}`)
    const networkConstants = chainToNetworkConstantsMap[chain]
    this.setConstants(networkConstants)
    this.setContract(chain, (networkConstants as any).SYSTEM_DOMAIN)
  }

  setCommon (chain: string) {
    const commonConstants = getCommonConstants(chain)
    this.setConstants(commonConstants)
  }

  setContract (chain: string, systemDomain = DEFAULT_SYSTEM_DOMAIN) {
    const contractConstants = getContractConstants(chain, systemDomain)
    this.setConstants(contractConstants)
  }

  setConstants (newConstants: object) {
    for (const [key, value] of Object.entries(newConstants)) {
      this[key as string] = value
    }
  }
}

export const constants = new Constants()
